import * as React from "react";
import Layout from "../../components/layout";
import { MDXRenderer } from "gatsby-plugin-mdx";
import { GatsbyImage, getImage, IGatsbyImageData } from "gatsby-plugin-image";
import { graphql } from "gatsby";
import * as Styles from "./blog.module.scss";

const BlogPost = ({ data }: any) => {
  const defaultImage: IGatsbyImageData = {
    layout: "constrained",
    width: 0,
    height: 0,
    images: {},
  };

  // image collection is broken
  let image = defaultImage;
  if (typeof data.mdx.frontmatter.hero_image == "undefined") {
    image = getImage(data.mdx.frontmatter.hero_image) ?? defaultImage;
  }

  return (
    <Layout pageTitle={data.mdx.frontmatter.title}>
      <GatsbyImage
        image={image}
        alt={data.mdx.frontmatter.hero_image_alt ?? ""}
      />
      <p className={Styles.date}>{data.mdx.frontmatter.date}</p>
      <MDXRenderer>{data.mdx.body}</MDXRenderer>
    </Layout>
  );
};

// don't filter out publish == 0 here, let the pages disperse without connection to the web
export const query = graphql`
  query ($id: String) {
    # mdx(id: { eq: $id }, frontmatter: { publish: { eq: 1 } }) {
    mdx(id: { eq: $id }) {
      frontmatter {
        title
        date(formatString: "MMMM D, YYYY")
        hero_image_alt
      }
      body
    }
  }
`;
export default BlogPost;
